import React, { useState } from "react";
import { createReview } from "@/lib/server/actions";

interface CreateReviewProps {
  onReviewCreated: () => void;
}

const CreateReview = ({ onReviewCreated }: CreateReviewProps) => {
  const [name, setName] = useState("");
  const [city, setCity] = useState("");
  const [description, setDescription] = useState("");
  const [rate, setRate] = useState<number>(1);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError(null);

    try {
      await createReview(name, city, description, rate);
      onReviewCreated();
    } catch (err) {
      setError("Failed to create review. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="flex flex-col gap-4">
      {error && <div className="alert alert-error">{error}</div>}
      <div className="form-control">
        <label className="label">Navn</label>
        <input
          type="text"
          className="input input-bordered"
          value={name}
          onChange={(e) => setName(e.target.value)}
          required
        />
      </div>
      <div className="form-control">
        <label className="label">By</label>
        <input
          type="text"
          className="input input-bordered"
          value={city}
          onChange={(e) => setCity(e.target.value)}
        />
      </div>
      <div className="form-control">
        <label className="label">Beskrivelse</label>
        <textarea
          className="textarea textarea-bordered"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
        />
      </div>
      <div className="form-control">
        <label className="label">Vurdering</label>
        <input
          type="number"
          className="input input-bordered"
          value={rate}
          onChange={(e) => setRate(Number(e.target.value))}
          min={1}
          max={5}
          required
        />
      </div>
      <button type="submit" className="btn btn-primary" disabled={loading}>
        {loading ? "Opretter..." : "Opret anmeldelse"}
      </button>
    </form>
  );
};

export default CreateReview;
